import { ComponentListItem } from "@/custom-component/component-list";
import config from "@/config";
import { cloneDeep } from "lodash";
import { createUUID } from "@/utils/utils";

// 项目配置信息字段
export interface ProjectConfig {
  _id?: string;
  name: string;
  title: string;
  description: string;
  coverImage: string;
  author: string;
  script: string;
  width: number;
  height: number;
  scale: number;
  pages: PageConfig[];
  // 翻页方式
  flipType: FlipType;
  // 访问状态
  status: Status;
  // 是否发布
  isPublish?: boolean;
  [x: string]: any;
}

export enum FlipType {
  UpDown = 0,
  LeftRight = 1,
}

export enum Status {
  Allow = 0,
  NotAllow = 1,
}

const projectConfig: ProjectConfig = {
  name: "",
  title: "未命名场景",
  description: "我用Luckh5可视化编辑器做了一个炫酷的H5，快来看看吧~",
  coverImage: "",
  author: "",
  script: "",
  scale: 100,
  width: config.canvasH5Width,
  height: config.canvasH5Height,
  pages: [],
  flipType: FlipType.UpDown,
  status: Status.Allow,
};

// 页面配置信息字段
export interface PageConfig {
  uuid?: string;
  name: string;
  componentData: ComponentListItem[];
  pageStyle: {
    color: string;
    opacity: number;
    fontSize: number;
    backgroundColor: string;
    backgroundImage: string;
    backgroundSize: string;
  };
  config: any;
}

const pageConfig: PageConfig = {
  name: "",
  componentData: [],
  pageStyle: {
    color: "#000000",
    opacity: 1,
    fontSize: 14,
    backgroundColor: "#4A90E2",
    backgroundImage: "",
    backgroundSize: "cover",
  },
  config: {},
};

export const getProjectConfig: () => ProjectConfig = () => {
  let project = cloneDeep(projectConfig);
  let onePage = getPageConfig();
  project.pages.push({
    ...onePage,
  });
  return project;
};

export const getPageConfig: () => PageConfig = () => {
  return {
    uuid: createUUID(),
    ...cloneDeep(pageConfig),
  };
};

export const copyPage = (data?: PageConfig) => {
  if (data) {
    const pageData = cloneDeep(data);
    pageData.uuid = createUUID();
    return pageData;
  }
  return getPageConfig();
};
