/**
 * 图片相关api
 */
import $axios from "@/service/httpServer";

// 获取我的素材
export const getMyImages = () => $axios.get("/luckyh5/image/getMyImages");

// 上传图片
export const uploadImage = (params: any) =>
  $axios.post("/luckyh5/image/uploadImage", params);

// 删除图片
export const deleteImage = (params: any) =>
  $axios.post("/luckyh5/image/deleteImage", params);

// 发布图片
export const publishImage = (params: any) =>
  $axios.post("/luckyh5/image/setPublish", params);

// 获取发布的图片
export const getPublishImages = () =>
  $axios.get("/luckyh5/image/getPublishImages");

// 添加至我的素材
export const addImageToMy = (params: any) =>
  $axios.post("/luckyh5/image/addImageToMy", params);
